import React from 'react'
import { Outlet } from 'react-router-dom'
import Header from '../../Sub/Header/Header'
import { CustomSnackBar } from '../../Sub'

function MainLayout({ snack, setSnack }) {
  return (
    <>
      <Header setSnack={setSnack} />
      <CustomSnackBar onClose={() => setSnack({ ...snack, open: false })} {...snack} />
      <Outlet />
    </>
  )
}

export default MainLayout
